.share-screen-btn {
  width: 52px;
  height: 11px;
  margin: 5px;
  font-family: Lato;
  font-size: 9px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #000062;
  white-space: nowrap;
  display: inline-block;
  justify-content: space-between;
}

.share-users-container {
  position: absolute;
  top: 21px;
  left: 50%;
  width: 600px;
  height: 85px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f7f7f7;
  border-radius: 10px;
  box-shadow: 0px 0px 4px #333;
  padding: 0px 25px;
}
.sharer-users-container {
  position: absolute;
  left: 50%;
  width: 600px;
  height: 85px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 10px;
  box-shadow: 0px 0px 4px #333;
  padding: 0px 0px 0px 25px;
}
.share-users-container p {
  margin: 0px;
  padding: 0px;
  font-family: "Lato";
  font-size: 12px;
}
.share-btn-box {
  width: auto;
  text-align: center;
}
.share-stop-btn {
  font-family: "Lato";
  width: 55px;
  height: 55px;
  background: #b02318;
  border-radius: 100%;
  color: #fff;
  text-transform: uppercase;
  font-size: 16px;
  outline: none;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.share-main-users {
  width: auto;
  height: auto;
  text-align: center;
  position: relative;
  display: flex;
}
.share-users {
  width: 78px;
  height: 78px;
  border-radius: 50%;
  background-image: linear-gradient(
    163deg,
    rgba(0, 0, 98, 0.06) 9%,
    rgba(0, 0, 98, 0.12) 92%
  );
  object-fit: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 8px;
}
.share-users img {
  width: 100%;
  border-radius: 50%;
}

.share-user1 {
  width: 55px;
  height: 55px;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  /* margin-right: 8px; */
  display: flex;
}
.share-user1 img {
  width: 100%;
  border-radius: 50%;
}
.share-main-mic {
  width: 55px;
  height: 55px;
  justify-content: center;
  align-items: center;
}
.share-main-sharing {
  width: auto;
  height: auto;
  text-align: center;
  position: relative;
  display: flex;
}
.share-parent-container {
  width: 100%;
  height: 100%;
}
.share-video-container {
  position: absolute;
  background-color: black;
  width: 100%;
  height: 100%;
  text-align: center;
}

.share-main-sharing .video-dropdown {
  position: absolute;
  width: 100px;
  height: auto;
  display: none;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.3);
  border: solid 0.5px rgba(0, 0, 0, 0.15);
  background-color: #fff;
  border-radius: 2px;
  left: -32px;
  top: 76px;
  z-index: 999;
}
.share-main-sharing .video-dropdown:before {
  content: "";
  background: url("../images/menu-icon.png") no-repeat;
  width: 34px;
  height: 16px;
  position: absolute;
  top: -16px;
  right: 7px;
}
.share-main-sharing:hover .video-dropdown {
  display: block;
}
.dropbox-video-container {
  padding: 10px;
}
.video-box-new {
  display: flex;
  justify-content: center;
}
.dropdown-user-video {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  margin-bottom: 40px;
  position: relative;
  margin-left: 7px;
  object-fit: cover;
}
.mrleft {
  margin-left: 10px;
}